import React from 'react';

export const Guideline = props => (
<div className="Center">
    <h2 className="GuideTitle">Guidelines</h2>
    <ul className="centerBlack">
        <li>Use a minimum password length of 10 or more characters if permitted.</li>
        <li>Include lowercase and uppercase alphabetic characters, numbers and symbols if permitted.</li>
        <li>Avoid character repetition, keyboard patterns, dictionary words, letter or number sequences.</li>
    </ul>
</div>
)


export const Problems = props => (
<div className="Center">
    <h2 className="GuideTitle">Problems</h2>
    <ul className="centerBlack">
        <li>Default passwords: password, default, admin, guest, etc.</li>
        <li>Dictionary words: chameleon, RedSox, sandbags, bunnyhop!, IntenseCrabtree, etc.</li>
        <li>Words with numbers appended: password1, deer2000, john1234, etc.</li>
        <li>Words with simple obfuscation: p@ssw0rd, l33th4x0r, g0ldf1sh, etc.</li>
        <li>Doubled words: crabcrab, stopstop, treetree, passpass, etc.</li>
        <li>Common sequences from a keyboard row: qwerty, 123456, asdfgh, fred, etc.</li>
        <li>Numeric sequences based on well known numbers such as 911 (9-1-1, 9/11), 314159... (pi), 27182... (e), 112 (1-1-2), etc.</li>
        <li>Identifiers: jsmith123, 1/1/1970, 555â€“1234, one's username, etc.</li>
        <li>Personally Identifiable Information (PII): license plate number, Social Security number, current or past telephone numbers, student ID, current address, etc.</li>
        <li>Dates: dates follow a pattern and make your password weak.</li>
    </ul>
</div>
)


export function expo(x, f) {
  return Number.parseFloat(x).toExponential(f);
}

export function financial(x) {
  return Number.parseFloat(x).toFixed(2);
}

// Check if string is valid.
export function checkString(string){
  if(string == null) {
    console.log("String is null.")
  } else if(string == "") {
    console.log("String is empty.")
  } else {
    return true;
  }
}

