import React from 'react';

// # 1 Random Words 
// # 2 Code Words
// # 3 Random Characters
export function generatePassword(type, length, dict, phrase){
    let str = "";
    switch (type){
      case 1:
        while(str.length < length){
          str += randomWord(dict);
        }
        break;
      case 2:
        for (var i = 0; i < length; i++){
          str += randomCharacter();
        }
        break;
      case 3:
        for (var i = 0; i < length; i++){
          str += passPhrase(dict, phrase.charAt(i));
        }
        break;
      default:
        break;
    }
    return str;
  }
export const Generator = props => (
  <div className="Center">
    <h2 className="GeneratorTitle">GENERATE PASSWORD</h2>
    <div>
      <button onClick={props.onclick} className="GeneratorButton">GENERATE</button>
      <input id="lenslide" className="slider" type="range" min="8" max="30" onChange={props.onchange} value={props.len}></input>
      <p className="lengthText">PASSWORD LENGTH: {props.len} </p>
      <select onChange={props.selectonchange} id="genSelect" name="generators" size="2">
          <option value="0">Random Words</option>
          <option value="1">Random Characters</option>
      </select>
    </div>
    <h2 hint="RESULT"className="GeneratorOutput">{props.generated}</h2>
  </div>
)  


function randomWord(dict){
    var keys = Object.keys(dict);
    var result = keys[keys.length * Math.random() << 0];
    return result;
}
  
function randomCharacter(){
    var chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789!@#$%^&*()_+';
    var charLen = chars.length;
    return chars.charAt(Math.floor(Math.random() * charLen));
}

function passPhrase(dict, char){
  char = char.toUpperCase();
  console.log(dict[char])
  if(dict.hasOwnProperty(char)){
    var result = dict[char][Math.floor(Math.random() * dict[char].length)];
  }
  return result;
}



  