import React from 'react';
import {pw_type, pw_strength, combinations, entropy} from './Analyzer';
import {Guideline, Problems, expo, financial, checkString} from './Misc';
import {generatePassword, Generator} from './Generator';
import {issues} from './issues';
import {makeDict} from './dict';
import {interpolateRgb, quantize} from "d3-interpolate";
import {buildStrength} from './strength';
import axios from 'axios';

import './App.css';
import './Generator.css';
import './strength.css';
import './Guidelines.css';
import './compare.css';

// Cracks per second.
const CPS = 588235;
// Word lookup object.
var dict = {};
// Object of letter arrays.
var alphadict = {}

const Header = props => (
  <header>
    <p onClick={props.onclick} className="logo">PASSCHECKER</p>
    <nav>
      <p onClick={props.onclick} className="nav-element">Strength</p>
      <p onClick={props.onclick} className="nav-element">Generator</p>
      <p onClick={props.onclick} className="nav-element">Compare</p>
      <p onClick={props.onclick} className="nav-element">Guidelines</p>
    </nav>
  </header>
)

const Compare = props => (
  <div className="Center">
    <h2 className="strtitle">COMPARE PASSWORDS</h2>
    <div>
      <input id="pass1" type="text" placeholder="ENTER PASSWORD"></input>
      <input id="pass2" type="text" placeholder="ENTER PASSWORD"></input>
    </div>
  </div>
)


class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      password  : "",
      generated : "",
      hide      : 0,
      len       : 16,
      genSelect : "0",
      page      : 0
    }

    this.handleChangeStrength = this.handleChangeStrength.bind(this);
    this.handlePageChange = this.handlePageChange.bind(this);
    this.handleClickGenerator = this.handleClickGenerator.bind(this);
    this.handleLengthChange = this.handleLengthChange.bind(this);
    this.handleTypeChange = this.handleTypeChange.bind(this);
    this.handleClickHide = this.handleClickHide.bind(this);
  }

  componentDidMount() {
    // Get the dictionary
    axios.get("http://gavingosling.me/data/dictionary.txt")
      .then(response => {
        let result = null;
        result = makeDict(response.data);
        dict = result.dict;
        alphadict = result.alphadict;
      })
      .catch(error => {
        console.log(error);
      })
  }

  render(){
    let page = this.state.page;
    let body = null;
    if(page == 0){
      let value = this.state.password;
      body = buildStrength(value, this.handleChangeStrength, this.state.hide, this.handleClickHide);
    }

    if(page == 1){
      body = <Generator selectonchange = {this.handleTypeChange} 
                        onclick={this.handleClickGenerator} 
                        generated={this.state.generated} 
                        onchange={this.handleLengthChange} 
                        len={this.state.len}>
             </Generator>
    }

    if(page == 2){
      body = <Compare></Compare>
    }

    if(page == 3){
      body = <div>
              <Guideline></Guideline>
              <Problems></Problems>
            </div>
    }

    return (
      <div className="App">
        <Header onclick={this.handlePageChange}></Header>
        {body}
      </div>
    );
  }

  handleChangeStrength(e) {
    var val = e.target.value;
    if(val.length > 26){
      val = val.substring(0, 26);
      e.target.value = val;
    }
    const value = analyzePassword(val);
    this.setState({
      ...this.state,
      password: value
    });
  }

  handleClickHide(){
    var x = document.getElementById("inputPass");
    let val = null;
    if (x.type === "password") {
      val = "text";
    } else {
      val = "password";
    }
    this.setState({
      ...this.state,
      hide: val
    });
  }

  handleClickGenerator(){
    let pass = "";
    switch(this.state.genSelect){
      case "0":
        pass = generatePassword(1, this.state.len, dict, null);
        break;
      case "1":
        pass = generatePassword(2, this.state.len, dict, null);
        break;
      default:
        console.log("Case not found.")
        break;
    }
    this.setState({
      ...this.state,
      generated: pass
    });
  }

  handleLengthChange(e){
    let val = document.getElementById("lenslide").value;
    this.setState({
      ...this.state,
      len: val
    });
  }

  handleTypeChange(e){
    let val = document.getElementById("genSelect").value;
    console.log(val);
    this.setState({
      ...this.state,
      genSelect: val
    });
  }

  /*
  case "3":
        if(/^[a-z]+$/i.test(input.value)){
          pass = generatePassword(3, this.state.len, alphadict, input.value);
        } else {
          console.log("ERROR, ONLY LETTERS ALLOWED")
        }
  */

  handlePageChange(e){
    const val = e.target.innerText;
    var page = null;
    switch(val){
      case "PassChecker":
        page = 0;
        break;
      case "Strength":
        page = 0;
        break;
      case "Generator":
        page = 1;
        break;
      case "Compare":
        page = 2;
        break;
      case "Guidelines":
        page = 3;
        break;
      default:
        page = 0;
        break;
    }
    this.setState({
      ...this.state,
      page: page
    });
  }
}

// Sort passwords base on length
function comparePass(a, b){
  console.log(typeof(a.bits))
  if(Number(a.bits) < Number(b.bits)){
    return 1;
  }
  if(Number(a.bits) > Number(b.bits)){
    return -1;
  }
  return 0;
}

// Analyze Password
function analyzePassword(e){
  if(e != null){
    let text = e;
    if(checkString(text)){
      let charpool = pw_type(text);
      let length = text.length;
      let bits = entropy(length, charpool.amount);
      let comb = combinations(length, charpool.amount);
      let crack = cracktime(comb)
      crack = expo(crack, 2);
      let obj = pw_strength(bits);
      let style = addPassword(obj.value);
      bits = financial(bits);
      let issue = issues(text, charpool.types, length, dict);
      return {name: text, strength: obj.strength, cracktime: crack, bits:bits,barStyle: style, issues: issue};
    }
  }
}

// Password Hue
function passHue(value){
  let colors = quantize(interpolateRgb("red", "green"), 5);
  return colors[value]
}

// Passbar style
function addPassword(value){
  let percentage = 25*(value+1);
  let hue = passHue(value);
  let style = {
    width: percentage + "%",
    background: hue
  }
  return style;
}

// Amount of time in seconds until crack.
function cracktime(comb){
  return comb/CPS;
}



export default App;
